/**
 * Component for displaying a pie chart
 */

import { makeStyles } from "@material-ui/core";
import { Cell, Pie, PieChart, Tooltip } from "recharts";

const useStyles = makeStyles(theme => ({
    root: {
    },
    smallImage: {
      maxWidth: 'calc(50% - 10px)',
      padding: '5px',
      cursor: 'pointer',
      opacity: 1,
      transition: 'opacity 1s',
      '&:hover': {
        opacity: 0.8,
        transition: 'opacity 300ms',
      },
    },
    fullscreenImage: {
      left: '0px',
      right: '0px',
      marginRight: 'auto',
      marginLeft: 'auto',
      width: '80vw',
      height: 'auto',
      top: '10vh',
      position: 'absolute',
      cursor: 'pointer',
    },
  }));
  
  interface PersonalityChartProps {
  }

  interface ChartData {
    cx: number;
    cy: number;
    midAngle: number;
    innerRadius: number;
    outerRadius: number;
    percent: number;
    index: number;
  }

  interface ChartSeriesData {
    name: string;
    value: number;
  }
  
  export default function PersonalityChart(props: PersonalityChartProps) {
    const classes = useStyles();

    const COLORS = ['#0088FE', '#00C49F', '#b553ed', '#2db120'];
    const RADIAN = Math.PI / 180;

    const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }: ChartData) => {
      const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
      const x = cx + radius * Math.cos(-midAngle * RADIAN);
      const y = cy + radius * Math.sin(-midAngle * RADIAN);
    
      return (
        <text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
          {`${(percent * 100).toFixed(0)}%`}
        </text>
      );
    };

    const data: ChartSeriesData[] = [
      { name: 'Problem Solver', value: 300 },
      { name: 'Creative', value: 400 },
      { name: 'Curious', value: 200 },
      { name: 'Artistic', value: 300 },
    ];
  
    return (
      <PieChart width={400} height={250}>
        <Pie
          data={data}
          isAnimationActive={false}
          cx="50%"
          cy="50%"
          labelLine={false}
          label={(entry: ChartSeriesData) => entry.name}
          outerRadius={80}
          innerRadius={60}
          fill="#8884d8"
          dataKey="value"
        >
          <Tooltip />
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
          ))}
        </Pie>
      </PieChart>
    )
  }