/**
 * Component for displaying a pie chart
 */

import { makeStyles } from "@material-ui/core";
import { Cell, Pie, PieChart, Tooltip } from "recharts";

const useStyles = makeStyles(theme => ({
    root: {
    },
    smallImage: {
      maxWidth: 'calc(50% - 10px)',
      padding: '5px',
      cursor: 'pointer',
      opacity: 1,
      transition: 'opacity 1s',
      '&:hover': {
        opacity: 0.8,
        transition: 'opacity 300ms',
      },
    },
    fullscreenImage: {
      left: '0px',
      right: '0px',
      marginRight: 'auto',
      marginLeft: 'auto',
      width: '80vw',
      height: 'auto',
      top: '10vh',
      position: 'absolute',
      cursor: 'pointer',
    },
  }));
  
  interface PersonalityChartProps {
  }

  interface ChartData {
    cx: number;
    cy: number;
    midAngle: number;
    innerRadius: number;
    outerRadius: number;
    percent: number;
    index: number;
  }

  interface ChartSeriesData {
    name: string;
    value: number;
  }
  
  export default function SkillsChart(props: PersonalityChartProps) {
    const classes = useStyles();

    const COLORS = ['#df4b4b', '#0088FE', '#FF8042', '#00C49F'];

    const data: ChartSeriesData[] = [
      { name: 'User Experience', value: 300 },
      { name: 'User Interfaces', value: 400 },
      { name: '3D Modeling', value: 200 },
      { name: 'Graphic Design', value: 300 },
    ];
  
    return (
      <PieChart width={400} height={250}>
        <Pie
          data={data}
          isAnimationActive={false}
          cx="50%"
          cy="50%"
          labelLine={false}
          label={(entry: ChartSeriesData) => entry.name}
          outerRadius={80}
          innerRadius={60}
          fill="#8884d8"
          dataKey="value"
        >
          <Tooltip />
          {data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
          ))}
        </Pie>
      </PieChart>
    )
  }