/**
 * Component for displaying a bar chart
 */

import { makeStyles } from "@material-ui/core";
import { Bar, BarChart, Tooltip, XAxis } from "recharts";

const useStyles = makeStyles(theme => ({
    root: {
    },
    smallImage: {
      maxWidth: 'calc(50% - 10px)',
      padding: '5px',
      cursor: 'pointer',
      opacity: 1,
      transition: 'opacity 1s',
      '&:hover': {
        opacity: 0.8,
        transition: 'opacity 300ms',
      },
    },
    fullscreenImage: {
      left: '0px',
      right: '0px',
      marginRight: 'auto',
      marginLeft: 'auto',
      width: '80vw',
      height: 'auto',
      top: '10vh',
      position: 'absolute',
      cursor: 'pointer',
    },
  }));
  
  interface SkillsChartProps {
  }

  interface ChartData {
    cx: number;
    cy: number;
    midAngle: number;
    innerRadius: number;
    outerRadius: number;
    percent: number;
    index: number;
  }

  interface ChartSeriesData {
    name: string;
    react?: number;
    typescript?: number;
    java?: number;
    cplusplus?: number;
    python?: number;
  }
  
  export default function SkillsChart(props: SkillsChartProps) {
    const classes = useStyles();

    const COLORS = ['#0088FE', '#00C49F', '#b553ed', '#2db120'];
    const RADIAN = Math.PI / 180;

    const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }: ChartData) => {
      const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
      const x = cx + radius * Math.cos(-midAngle * RADIAN);
      const y = cy + radius * Math.sin(-midAngle * RADIAN);
    
      return (
        <text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central">
          {`${(percent * 100).toFixed(0)}%`}
        </text>
      );
    };

    const data: ChartSeriesData[] = [
      {
        name: 'React',
        react: 90,
      },
      {
        name: 'Typescript',
        typescript: 100,
      },
      {
        name: 'Java',
        java: 60,
      },
      {
        name: 'C++',
        cplusplus: 40,
      },
      {
        name: 'Python',
        python: 40,
      },
    ];
  
    return (
      <BarChart
        width={350}
        height={250}
        data={data}
        margin={{
          top: 5,
          right: 30,
          left: 20,
          bottom: 5,
        }}
      >
        {/* <CartesianGrid strokeDasharray="3 3" /> */}
        <XAxis dataKey="name" />
        {/* <YAxis /> */}
        <Tooltip />
        {/* <Legend /> */}
        <Bar dataKey="react" stackId="a" fill="#0088FE" />
        <Bar dataKey="typescript" stackId="a" fill="#00C49F" />
        <Bar dataKey="java" stackId="a" fill="#b553ed" />
        <Bar dataKey="cplusplus" stackId="a" fill="#2db120" />
        <Bar dataKey="python" stackId="a" fill="#82ca9d" />
      </BarChart>
    )
  }